/* Reset some default styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f5f7;
  color: #333;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 1rem;
}

/* Navbar */
.navbar {
  background-color: #026aa7;
  color: #fff;
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar a {
  color: #fff;
  margin: 0 0.5rem;
}

/* General Layout */
header,
footer {
  background-color: #026aa7;
  color: #fff;
  text-align: center;
  padding: 1rem 0;
}

main {
  padding: 1rem 0;
}

/* Forms */
form {
  background: #fff;
  padding: 2rem;
  margin: 1rem 0;
  border-radius: 0.5rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

form input,
form select,
form button {
  display: block;
  width: 100%;
  padding: 0.75rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 0.5rem;
}

form button {
  background-color: #5aac44;
  color: #fff;
  border: none;
  cursor: pointer;
}

form button:hover {
  background-color: #519839;
}

/* Lists */
ul {
  list-style-type: none;
}

li {
  background: #fff;
  margin: 0.5rem 0;
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Kanban Board */
.kanban-board {
  display: flex;
  overflow-x: auto;
  padding: 1rem 0;
}

.kanban-column {
  background: #e2e4e6;
  margin-right: 1rem;
  border-radius: 0.5rem;
  flex: 0 0 300px;
  padding: 1rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.kanban-column h2 {
  margin-bottom: 1rem;
  color: #333;
}

.task-card {
  background: #fff;
  padding: 1rem;
  margin-bottom: 0.5rem;
  border-radius: 0.5rem;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.task-card h3 {
  margin-bottom: 0.5rem;
}

/* Checklist */
.checklist {
  background: #fff;
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.checklist ul {
  list-style-type: none;
}

.checklist li {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
}

.checklist input[type="checkbox"] {
  margin-right: 0.5rem;
}

/* Reports */
.report {
  background: #fff;
  padding: 2rem;
  margin: 1rem 0;
  border-radius: 0.5rem;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.report ul {
  list-style-type: none;
}

.report li {
  background: #f9f9f9;
  margin-bottom: 0.5rem;
  padding: 1rem;
  border-radius: 0.5rem;
}

/* Mobile-first design */
@media (min-width: 768px) {
  .kanban-board {
    flex-direction: row;
  }
}

@media (min-width: 1024px) {
  .container {
    width: 80%;
  }
}
