/* LoginPage.css */
.login-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f0f2f5;
  }
  
  .login-form {
    background: #fff;
    padding: 2rem;
    border-radius: 0.5rem;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 400px;
    text-align: center;
  }
  
  .login-form h2 {
    margin-bottom: 1.5rem;
    color: #026aa7;
  }
  
  .login-form .form-group {
    margin-bottom: 1.5rem;
    text-align: left;
  }
  
  .login-form .form-group label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: bold;
  }
  
  .login-form .form-group input {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid #ccc;
    border-radius: 0.25rem;
    font-size: 1rem;
  }
  
  .login-form .form-group input:focus {
    border-color: #026aa7;
    outline: none;
    box-shadow: 0 0 5px rgba(2, 106, 167, 0.1);
  }
  
  .login-form button {
    width: 100%;
    padding: 0.75rem;
    background: #026aa7;
    border: none;
    border-radius: 0.25rem;
    color: #fff;
    font-size: 1rem;
    cursor: pointer;
    transition: background 0.3s;
  }
  
  .login-form button:hover {
    background: #025a91;
  }
  