/* CreateBoard.css */
.create-board {
    padding: 2rem;
  }
  
  .create-board h1 {
    text-align: center;
  }
  
  .form-group {
    margin: 1rem 0;
  }
  
  .form-group label {
    display: block;
    margin-bottom: 0.5rem;
  }
  
  .form-group input,
  .form-group textarea {
    width: 100%;
    padding: 0.5rem;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .templates {
    margin: 2rem 0;
  }
  
  .templates h2 {
    text-align: center;
  }
  
  .template-item {
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 1rem;
    margin: 0.5rem 0;
    cursor: pointer;
  }
  
  .template-item.selected {
    border-color: #007bff;
    background-color: #e9f7ff;
  }
  