/* Dashboard */
.dashboard {
    padding: 2rem;
  }
  
  .dashboard h1 {
    text-align: center;
    margin-bottom: 2rem;
  }
  
  .dashboard-section {
    background: #fff;
    padding: 1.5rem;
    margin-bottom: 1.5rem;
    border-radius: 0.5rem;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .dashboard-section h2 {
    margin-bottom: 1rem;
    color: #026aa7;
  }
  
  .dashboard-section p {
    font-size: 1.5rem;
    color: #333;
  }
  
  .dashboard-section ul {
    list-style-type: none;
    padding: 0;
  }
  
  .dashboard-section li {
    background: #f9f9f9;
    margin-bottom: 0.5rem;
    padding: 1rem;
    border-radius: 0.5rem;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  }
  